import React, {Component} from 'react'
import {DataTableResponsive} from 'tsi2-ui-library'

import './css/bootstrap.css'
import './js/plugins/jquery-ui/jquery.ui.1.10.2.ie.css'
import './css/main.css'
import './css/plugins.css'
import './css/responsive.css'
import './css/icons.css'
import './css/fontawesome/font-awesome.min.css'

export default class App extends Component {
  componentDidMount() {
    const scripts = [
      'js/libs/jquery-3.4.1.min.js',
      'js/plugins/jquery-ui/jquery-ui-1.10.2.custom.min.js',
      'js/libs/bootstrap.min.js',
      'js/libs/lodash.compat.min.js',
      'js/plugins/touchpunch/jquery.ui.touch-punch.min.js',
      'js/plugins/event.swipe/jquery.event.move.js',
      'js/plugins/event.swipe/jquery.event.swipe.js',
      'js/libs/breakpoints.js',
      'js/plugins/respond/respond.min.js',
      'js/plugins/cookie/jquery.cookie.min.js',
      'js/plugins/slimscroll/jquery.slimscroll.min.js',
      'js/plugins/slimscroll/jquery.slimscroll.horizontal.min.js',
      'js/app',
      'js/plugins.js',
      'js/plugins.form-components.js'
    ]

    scripts.forEach(function (value) {
      let script = document.createElement('script')
      script.src = value
      script.type = 'text/javascript'
      document.body.appendChild(script)
    })
  }

  render() {
    return (
      <DataTableResponsive columns={[]} rows={[]} />
    )
  }
}



